<template>
            <el-select v-model="inputvalue" placeholder="">
            <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
            </el-select>
            <el-button @click="inputvalue=null"  ></el-button>
            <el-button @click="openWindow"  type="primary"></el-button>
    <el-card style="padding-bottom: 15%;">
        <el-table :data="clothingList.filter(data=>!inputvalue||(data.season==inputvalue))" table-layout="fixed" :show-overflow-tooltip="false" 
    :row-style="{height:'30px'}" :cell-style="{padding:'0px'}"  style="font-size: 10px;" row-key="clothingid"
    fit  border >
        
        <el-table-column label="" min-width="35">
            <template #default="scope" >
                <el-image style="height: 30px; display: block; margin: 0 auto;" :src="scope.row.url" :zoom-rate="1.2"
            :preview-src-list="scope.row.srcList" :initial-index="4" fit="contain" :preview-teleported="true" />
            </template>
        </el-table-column>
        <el-table-column prop="name" label="" :filters="getfildername" :filter-method="filterHandler" />
        <el-table-column prop="size" label=""  />
        <el-table-column prop="body_width" label=""  />
        <el-table-column prop="clothing_length" label=""  />
        <el-table-column prop="shoulder_length" label="" />
        <el-table-column prop="sleeve_length" label=""  />
        <el-table-column label="" >
            <template #default="scope">
                <el-button style="display:block;margin: 0 auto;" size="small"  @click="handleEdit(scope.$index,scope.row.id, true)" ></el-button>
            </template>
        </el-table-column>
        <el-table-column label="" >
            <template #default="scope">
                <el-button style="display:block;margin: 0 auto;" size="small" @click="handleEdit(scope.$index,scope.row.id, false)"></el-button>
            </template>
        </el-table-column>
        <el-table-column label="" >
            <template #default="scope">
                <el-button style="display:block;margin: 0 auto;" size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
        </el-table-column>
    
    </el-table>
    </el-card>

    <el-dialog  v-model="windowVisible" :append-to-body="true" width="100%" height="50vh" >
             <EditCoatView v-if="windowVisible" ref="popWindow" :fetchData="fetchData" :inputdisable="inputdisable"></EditCoatView>
              <span slot="footer" class="dialog-footer" v-if="!inputdisable">
                  <el-button type="primary" @click="postform"></el-button>
                  <el-button @click="windowVisible = false"></el-button>
              </span>
    </el-dialog>

</template>
  
<script lang="ts" setup>
import { ref,nextTick,onMounted,computed, Ref  } from 'vue';
import EditCoatView from './EditCoatView.vue';
import axios from 'axios';
import { useRoute } from 'vue-router';
import Sortable from 'sortablejs'

const windowVisible = ref(false);
const inputdisable = ref(false);
const popWindow = ref();
const options=[{value: '0', label: ''},
                     {value: '1', label: ''},  
                     {value: '2', label: ''}
                    ];
const inputvalue=ref();
const orderref=ref();

interface Clothing {
    id:Number
    clothingid:Number
    name: string
    url: string
    srcList: string[]
    size:string
    body_width:string
    clothing_length:string
    shoulder_length:string
    sleeve_length:string
    season:string

}

const clothingList:Ref<Clothing[]>  = ref<Clothing[]>([]);

const tagid= ref<Number>();
var coatlist:any = [];
const fetchData = async (tagid:Number|undefined) => {
    if(!tagid){
        try {
        const headers={'userid':localStorage.getItem('userid')}
        const response = await axios.get('/v1/coat/list',{headers:headers});
        const responseData = response.data;
        coatlist=responseData
      } catch (error) {
        console.error(error);
      }
    }else{
        try {
        const response = await axios.get('/v1/tags/getclothingfrontag',{params:{tagid:tagid,type:0}});
        const responseData = response.data;
        coatlist=responseData
      } catch (error) {
        console.error(error);
      }
    }
    if(coatlist){
    clothingList.value = coatlist.map((item:any) => ({
          id: item.id,
          clothingid: item.clothing.id,
          name: item.clothing.name,
          url: item.clothing.url,
          srcList: item.clothing.srcList ? JSON.parse(item.clothing.srcList) : [],
          size:item.clothing.size,
          body_width:item.bodyWidth,
          clothing_length:item.clothingLength,
          shoulder_length:item.shoulderWidth,
          sleeve_length:item.sleeveLength,
          season:item.clothing.season
        }));}
    sortArry(clothingList.value)

    };

onMounted(()=>{
    const route = useRoute();
    tagid.value=Number(route.query.tagid)
    console.log(tagid.value)
    fetchData(tagid.value)
    initSort()

    
})

const  openWindow= () =>{
        windowVisible.value=true
        inputdisable.value=false
        nextTick(()=>{
        popWindow.value.newInit(0)
    })
      }

    const handleEdit = (index: number,coatid: Number,edit:boolean) => {
    const coat=coatlist.filter((item:any) => item.id == coatid)[0]
    windowVisible.value=true
    inputdisable.value=edit
    nextTick(()=>{
        popWindow.value.dataInit(coat)
    })
}

const handleDelete = (index: number, row: Clothing) => {
    if(confirm('?')){
        const url=`/v1/deleted/${row.clothingid}`
        axios.get(url)
        .then(response=>{
            console.log(response.data)
            fetchData(tagid.value)
        })
        .catch(error=>{
            throw new Error(error.response.data)
        })
    }

}
const  postform = async () => {
    let msg=await popWindow.value.updataform();
    console.log(msg)
    if(msg){
        windowVisible.value=false
    }
 
}

const filterHandler=(value:any, row:any, column:any)=>{
        const property = column['property'];
        return row[property]===value;
      }

const  initSort=()=> {
        const el = document.querySelectorAll('.el-table__body-wrapper  table  tbody')[0]
        // const sortable = new Sortable(el, options);
        // options
        const sortable = new Sortable(el, {
        handle: '.el-image',
        animation: 500,
        delay:30,
        onEnd: (e:any) => { // 
            const arr = clothingList.value; // 
            arr.splice(e.newIndex, 0, arr.splice(e.oldIndex, 1)[0]); // 
            nextTick(() => {
            clothingList.value = arr;
            console.log(clothingList.value);
            const order =clothingList.value.map((item:any)=>{
                return item.clothingid
            })
            console.log(order)
            saveorder(order)

        });
        }
        })
    }

const sortArry=(arry:Array<Number>|any)=>{
    const headers={'userid':localStorage.getItem('userid')}
    axios.get(`/v1/sort/0`,{headers:headers}).then(
        (res:any)=>{
            orderref.value=res.data
            let order=orderref.value.sortarry
            console.log(order)
            const filteredArr = clothingList.value;
            filteredArr.sort((a, b) => {
            const indexA = order.indexOf(a.clothingid);
            const indexB = order.indexOf(b.clothingid);
            if (indexA == -1 && indexB === -1) {
                return 0; //  id  order 
            } else if (indexA === -1) {
                return 1; //  a  id  order  a  b 
            } else if (indexB === -1) {
                return -1; //  b  id  order  b  a 
            } else {
                return indexA - indexB; //  id  order  order 
            }
            });
        }
    )
}

const saveorder=(arry:Array<Number>|any)=>{
    let order=orderref.value
    const data={'id':order?order.id:null,"type":order?order.type:0,"userid":localStorage.getItem("userid"),'sortarry':JSON.stringify(arry)}
    axios.post("/v1/sort",data).then(
        (res:any)=>{
            console.log(res.data)
        })
}

const getfildername=computed(()=>{
    const uniqueClothingNames = [...new Set(clothingList.value.map(item => item.name))];
    console.log(uniqueClothingNames)
    return uniqueClothingNames.map((item:any)=>{
        return {
            text:item,
            value:item
        }
    })
})


    

</script>

<style>
.el-table .cell.el-tooltip{
    padding:0;
}
.el-table td.el-table__cell div{
    padding: 0;
    text-align: center;
}
.el-table .cell{
    text-align: center;
}
</style>