import { createRouter, createWebHashHistory, RouteRecordRaw } from "vue-router";
import Index from "../views/IndexView.vue";
import List from "../views/ListPage.vue";
import Seasonal from "../views/SeasonalView.vue";
import Recycle from "../views/RecycleView.vue";
import User from "../views/UserView.vue";
import TagList from "../views/TagListView.vue";
 
const routes: Array<RouteRecordRaw> = [
  {
    path: "/home",
    name: "home",
    component: Index,
    meta: { requiresAuth: true,title:'' }
  },
  {
    path: "/",
    redirect:'/home',
    meta:{title:''}
  },
  {
    path: "/user",
    name:'user',
    component: User,
    meta:{title:''}
    
  },
  {
    path: "/list/coat",
    name:'coat',
    component: List,
    meta: { requiresAuth: true ,title:''}
    
  },
  {
    path: "/list/pants",
    'name':'pants',
    component: List,
    meta: { requiresAuth: true,title:'' }
  },
  {
    path: "/list/underwear",
    'name':'underwear',
    component: List,
    meta: { requiresAuth: true,title:'' }
  },
  {
    path: "/list/shoe",
    'name':'shoe',
    component: List,
    meta: { requiresAuth: true,title:'' }
  },
  {
    path: "/seasonal",
    name: "seasonal",
    component:Seasonal,
    meta: { requiresAuth: true ,title:''}
  },
  {
    path: "/recycle",
    name: "recycle",
    component:Recycle,
    meta: { requiresAuth: true,title:'' }
  },
  {
    path: "/taglist",
    'name':'taglist',
    component: TagList,
    meta: { title:'' }
  }
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

router.beforeEach((to, from, next) => {
  const isLoggedIn =localStorage.getItem("userid") /* localStoragetoken */
  console.log(to.meta.requiresAuth);

  if (to.meta.requiresAuth&&!isLoggedIn) {
    next('/user') // 
  } else {
    to.meta.title && (document.title = to.meta.title as string);
    next() // 
  }
})

export default router;
